import React, { useState, useEffect } from 'react';
import { Helmet } from 'react-helmet-async';
import {
    Alert,
    Avatar,
    Box,
    Button,
    Card,
    CardContent,
    Chip,
    Container,
    Dialog,
    DialogActions,
    DialogContent,
    DialogTitle,
    FormControl,
    Grid,
    IconButton,
    InputAdornment,
    InputLabel,
    MenuItem,
    Paper,
    Select,
    Snackbar,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    TextField,
    Tooltip,
    Typography
} from '@mui/material';
import {
    Cancel as RejectIcon,
    CheckCircle as ApproveIcon,
    Delete as DeleteIcon,
    Edit as EditIcon,
    Person as PersonIcon,
    Search as SearchIcon,
    Visibility as ViewIcon
} from '@mui/icons-material';
import { formatDistanceToNow } from 'date-fns';
import { useAuth } from 'src/contexts/AuthContext';
import { projectService } from 'src/services/projectService';

const AdminProjects = () => {
    const { user } = useAuth();
    
    const [projects, setProjects] = useState([]);
    const [filteredProjects, setFilteredProjects] = useState([]);
    const [loading, setLoading] = useState(true);
    const [searchTerm, setSearchTerm] = useState('');
    const [filterStatus, setFilterStatus] = useState('all');
    const [selectedProject, setSelectedProject] = useState(null);
    const [openRejectDialog, setOpenRejectDialog] = useState(false);
    const [openDeleteDialog, setOpenDeleteDialog] = useState(false);
    const [rejectReason, setRejectReason] = useState('');
    const [snackbar, setSnackbar] = useState({
        open: false,
        message: '',
        severity: 'success'
    });

    useEffect(() => {
        loadProjects();
    }, []);

    useEffect(() => {
        filterProjects();
    }, [projects, searchTerm, filterStatus]);

    const loadProjects = async () => {
        try {
            setLoading(true);
            const response = await projectService.getAllProjectsAdmin();
            setProjects(response.data);
        } catch (error) {
            console.error('Error loading projects:', error);
            setSnackbar({
                open: true,
                message: 'Failed to load projects',
                severity: 'error'
            });
        } finally {
            setLoading(false);
        }
    };

    const filterProjects = () => {
        let filtered = [...projects];

        // Filter by search term
        if (searchTerm) {
            filtered = filtered.filter(project =>
                project.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
                project.description.toLowerCase().includes(searchTerm.toLowerCase()) ||
                project.owner.email.toLowerCase().includes(searchTerm.toLowerCase())
            );
        }

        // Filter by status
        if (filterStatus !== 'all') {
            filtered = filtered.filter(project => project.status === filterStatus);
        }

        setFilteredProjects(filtered);
    };

    const handleApproveProject = async (projectId) => {
        try {
            await projectService.approveProject(projectId);
            setProjects(projects.map(project => 
                project.id === projectId 
                    ? { ...project, status: 'approved', is_approved: true }
                    : project
            ));
            setSnackbar({
                open: true,
                message: 'Project approved successfully!',
                severity: 'success'
            });
        } catch (error) {
            console.error('Error approving project:', error);
            setSnackbar({
                open: true,
                message: 'Failed to approve project',
                severity: 'error'
            });
        }
    };

    const handleRejectProject = async (projectId) => {
        try {
            await projectService.rejectProject(projectId, rejectReason);
            setProjects(projects.map(project => 
                project.id === projectId 
                    ? { ...project, status: 'rejected' }
                    : project
            ));
            setOpenRejectDialog(false);
            setRejectReason('');
            setSnackbar({
                open: true,
                message: 'Project rejected successfully!',
                severity: 'success'
            });
        } catch (error) {
            console.error('Error rejecting project:', error);
            setSnackbar({
                open: true,
                message: 'Failed to reject project',
                severity: 'error'
            });
        }
    };

    const handleDeleteProject = async (projectId) => {
        try {
            await projectService.deleteProjectAdmin(projectId);
            setProjects(projects.filter(project => project.id !== projectId));
            setOpenDeleteDialog(false);
            setSelectedProject(null);
            setSnackbar({
                open: true,
                message: 'Project deleted successfully!',
                severity: 'success'
            });
        } catch (error) {
            console.error('Error deleting project:', error);
            setSnackbar({
                open: true,
                message: 'Failed to delete project',
                severity: 'error'
            });
        }
    };

    const handleConfirmDelete = () => {
        if (selectedProject) {
            handleDeleteProject(selectedProject.id);
        }
    };

    const handleCancelDelete = () => {
        setOpenDeleteDialog(false);
        setSelectedProject(null);
    };

    const getStatusColor = (status) => {
        switch (status) {
            case 'approved':
                return 'success';
            case 'pending':
                return 'warning';
            case 'rejected':
                return 'error';
            case 'draft':
                return 'default';
            default:
                return 'default';
        }
    };

    const getProjectTypeIcon = (type) => {
        switch (type) {
            case 'code':
                return 'üíª';
            case 'text':
                return 'üìù';
            default:
                return 'üìÑ';
        }
    };

    return (
        <>
            <Helmet>
                <title>Admin Projects - OneDollarAI</title>
            </Helmet>

            <Container maxWidth="xl">
                <Box sx={{ mt: 3, mb: 4 }}>
                    <Typography variant="h2" component="h1" gutterBottom sx={{ color: 'black' }}>
                        Admin Projects
                    </Typography>
                    <Typography variant="body1" sx={{ color: 'black' }}>
                        Manage and review all projects in the system.
                    </Typography>
                </Box>

                <Card sx={{ mb: 3 }}>
                    <CardContent>
                        <Grid container spacing={2} alignItems="center">
                            <Grid item xs={12} md={6}>
                                <TextField
                                    fullWidth
                                    placeholder="Search projects by title, description, or owner..."
                                    value={searchTerm}
                                    onChange={(e) => setSearchTerm(e.target.value)}
                                    InputProps={{
                                        startAdornment: (
                                            <InputAdornment position="start">
                                                <SearchIcon sx={{ color: 'black' }} />
                                            </InputAdornment>
                                        ),
                                    }}
                                    sx={{
                                        '& .MuiInputBase-root': {
                                            color: 'black',
                                            '& fieldset': {
                                                borderColor: 'black'
                                            },
                                            '&:hover fieldset': {
                                                borderColor: 'black'
                                            },
                                            '&.Mui-focused fieldset': {
                                                borderColor: 'black'
                                            }
                                        },
                                        '& .MuiInputLabel-root': {
                                            color: 'black',
                                            '&.Mui-focused': {
                                                color: 'black'
                                            }
                                        },
                                        '& .MuiFormHelperText-root': {
                                            color: 'black'
                                        }
                                    }}
                                />
                            </Grid>
                            <Grid item xs={12} md={3}>
                                <FormControl fullWidth sx={{
                                    '& .MuiInputBase-root': {
                                        color: 'black',
                                        '& fieldset': {
                                            borderColor: 'black'
                                        },
                                        '&:hover fieldset': {
                                            borderColor: 'black'
                                        },
                                        '&.Mui-focused fieldset': {
                                            borderColor: 'black'
                                        }
                                    },
                                    '& .MuiInputLabel-root': {
                                        color: 'black',
                                        '&.Mui-focused': {
                                            color: 'black'
                                        }
                                    },
                                    '& .MuiSelect-icon': {
                                        color: 'black'
                                    }
                                }}>
                                    <InputLabel>Status</InputLabel>
                                    <Select
                                        value={filterStatus}
                                        onChange={(e) => setFilterStatus(e.target.value)}
                                        label="Status"
                                    >
                                        <MenuItem value="all" sx={{ color: 'black !important',
                                                '&:hover, &:active, &.active, &.Mui-selected': {
                                                    color: 'black !important'
                                                }
                                            }}>All Statuses</MenuItem>
                                        <MenuItem value="draft" sx={{ color: 'black !important',
                                                '&:hover, &:active, &.active, &.Mui-selected': {
                                                    color: 'black !important'
                                                }
                                            }}>Draft</MenuItem>
                                        <MenuItem value="pending" sx={{ color: 'black !important',
                                                '&:hover, &:active, &.active, &.Mui-selected': {
                                                    color: 'black !important'
                                                }
                                            }}>Pending Review</MenuItem>
                                        <MenuItem value="approved" sx={{ color: 'black !important',
                                                '&:hover, &:active, &.active, &.Mui-selected': {
                                                    color: 'black !important'
                                                }
                                            }}>Approved</MenuItem>
                                        <MenuItem value="rejected" sx={{ color: 'black !important',
                                                '&:hover, &:active, &.active, &.Mui-selected': {
                                                    color: 'black !important'
                                                }
                                            }}>Rejected</MenuItem>
                                    </Select>
                                </FormControl>
                            </Grid>
                            <Grid item xs={12} md={3}>
                                <Typography variant="body2" sx={{ color: 'black' }}>
                                    Total: {filteredProjects.length} projects
                                </Typography>
                            </Grid>
                        </Grid>
                    </CardContent>
                </Card>

                <TableContainer component={Paper} sx={{ 
                    backgroundColor: '#fff',
                    '& .MuiTableHead-root .MuiTableCell-root': {
                        backgroundColor: '#f5f5f5',
                        color: 'black',
                        fontWeight: 600
                    },
                    '& .MuiTableBody-root .MuiTableCell-root': {
                        color: 'black'
                    }
                }}>
                    <Table>
                        <TableHead>
                            <TableRow>
                                <TableCell>Project</TableCell>
                                <TableCell>Owner</TableCell>
                                <TableCell>Type</TableCell>
                                <TableCell>Status</TableCell>
                                <TableCell>Cost per use</TableCell>
                                <TableCell>Created</TableCell>
                                <TableCell>Actions</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {filteredProjects.map((project) => (
                                <TableRow key={project.id} sx={{ '&:hover': { backgroundColor: '#f8f9fa' } }}>
                                    <TableCell>
                                        <Box>
                                            <Typography variant="subtitle2" noWrap sx={{ color: 'black' }}>
                                                {project.title}
                                            </Typography>
                                            <Typography 
                                                variant="caption" 
                                                sx={{ color: 'black' }}
                                                noWrap
                                            >
                                                {project.description.substring(0, 50)}...
                                            </Typography>
                                        </Box>
                                    </TableCell>
                                    <TableCell>
                                        <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                                            <Avatar sx={{ width: 32, height: 32 }}>
                                                {project.owner.avatar ? (
                                                    <img 
                                                        src={project.owner.avatar} 
                                                        alt={project.owner.first_name} 
                                                    />
                                                ) : (
                                                    <PersonIcon fontSize="small" sx={{ color: 'black' }} />
                                                )}
                                            </Avatar>
                                            <Box>
                                                <Typography variant="body2" sx={{ color: 'black' }}>
                                                    {project.owner.first_name} {project.owner.last_name}
                                                </Typography>
                                                <Typography variant="caption" sx={{ color: 'black' }}>
                                                    {project.owner.email}
                                                </Typography>
                                            </Box>
                                        </Box>
                                    </TableCell>
                                    <TableCell>
                                        <Chip
                                            label={project.project_type}
                                            size="small"
                                            icon={
                                                <span>
                                                    {getProjectTypeIcon(project.project_type)}
                                                </span>
                                            }
                                            sx={{ color: 'black' }}
                                        />
                                    </TableCell>
                                    <TableCell>
                                        <Chip
                                            label={project.status}
                                            color={getStatusColor(project.status)}
                                            size="small"
                                        />
                                    </TableCell>
                                    <TableCell>
                                        <Typography variant="caption" sx={{ color: 'black' }}>
                                            ${project.price_per_use}/use
                                        </Typography>
                                    </TableCell>
                                    <TableCell>
                                        <Typography variant="body2" sx={{ color: 'black' }}>
                                            {formatDistanceToNow(
                                                new Date(project.created_at), 
                                                { addSuffix: true }
                                            )}
                                        </Typography>
                                    </TableCell>
                                    <TableCell>
                                        <Box sx={{ display: 'flex', gap: 0.5 }}>
                                            <Tooltip title="View Project">
                                                <IconButton 
                                                    size="small"
                                                    onClick={() => window.open(`/project/${project.id}`, '_blank')}
                                                >
                                                    <ViewIcon fontSize="small" sx={{ color: 'black' }} />
                                                </IconButton>
                                            </Tooltip>
                                            
                                            {project.status === 'pending' && (
                                                <>
                                                    <Tooltip title="Approve Project">
                                                        <IconButton
                                                            size="small"
                                                            color="success"
                                                            onClick={() => handleApproveProject(project.id)}
                                                        >
                                                            <ApproveIcon fontSize="small" />
                                                        </IconButton>
                                                    </Tooltip>
                                                    <Tooltip title="Reject Project">
                                                        <IconButton
                                                            size="small"
                                                            color="error"
                                                            onClick={() => {
                                                                setSelectedProject(project);
                                                                setOpenRejectDialog(true);
                                                            }}
                                                        >
                                                            <RejectIcon fontSize="small" />
                                                        </IconButton>
                                                    </Tooltip>
                                                </>
                                            )}
                                            
                                            <Tooltip title="Delete Project">
                                                <IconButton
                                                    size="small"
                                                    color="error"
                                                    onClick={() => {
                                                        setSelectedProject(project);
                                                        setOpenDeleteDialog(true);
                                                    }}
                                                >
                                                    <DeleteIcon fontSize="small" />
                                                </IconButton>
                                            </Tooltip>
                                        </Box>
                                    </TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </TableContainer>

                {filteredProjects.length === 0 && !loading && (
                    <Box sx={{ textAlign: 'center', mt: 4 }}>
                        <Typography variant="h6" sx={{ color: 'black' }} gutterBottom>
                            No projects found
                        </Typography>
                        <Typography variant="body2" sx={{ color: 'black' }}>
                            {searchTerm || filterStatus !== 'all' 
                                ? 'Try adjusting your search or filters'
                                : 'No projects in the system yet'
                            }
                        </Typography>
                    </Box>
                )}

                {/* Reject Dialog */}
                <Dialog
                    open={openRejectDialog}
                    onClose={() => setOpenRejectDialog(false)}
                    maxWidth="sm"
                    fullWidth
                >
                    <DialogTitle sx={{ color: 'black' }}>Reject Project</DialogTitle>
                    <DialogContent>
                        <Typography variant="body2" sx={{ mb: 2, color: 'black' }}>
                            Are you sure you want to reject "{selectedProject?.title}"?
                        </Typography>
                        <TextField
                            fullWidth
                            multiline
                            rows={3}
                            label="Reason for rejection (optional)"
                            value={rejectReason}
                            onChange={(e) => setRejectReason(e.target.value)}
                            placeholder="Please provide a reason for rejection..."
                            sx={{
                                '& .MuiInputBase-root': {
                                    color: 'black',
                                    '& fieldset': {
                                        borderColor: 'black'
                                    },
                                    '&:hover fieldset': {
                                        borderColor: 'black'
                                    },
                                    '&.Mui-focused fieldset': {
                                        borderColor: 'black'
                                    }
                                },
                                '& .MuiInputLabel-root': {
                                    color: 'black',
                                    '&.Mui-focused': {
                                        color: 'black'
                                    }
                                },
                                '& .MuiFormHelperText-root': {
                                    color: 'black'
                                }
                            }}
                        />
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={() => setOpenRejectDialog(false)} sx={{ color: 'black' }}>
                            Cancel
                        </Button>
                        <Button
                            onClick={() => handleRejectProject(selectedProject.id)}
                            color="error"
                            variant="contained"
                        >
                            Reject Project
                        </Button>
                    </DialogActions>
                </Dialog>

                {/* Delete Confirmation Dialog */}
                <Dialog
                    open={openDeleteDialog}
                    onClose={handleCancelDelete}
                    maxWidth="sm"
                    fullWidth
                >
                    <DialogTitle sx={{ 
                        color: 'black', 
                        backgroundColor: '#fff3cd',
                        display: 'flex',
                        alignItems: 'center',
                        gap: 1
                    }}>
                        <DeleteIcon sx={{ color: '#856404' }} />
                        Confirm Delete
                    </DialogTitle>
                    <DialogContent sx={{ backgroundColor: '#fff3cd', pt: 2 }}>
                        <Typography variant="body1" sx={{ color: 'black', mb: 2 }}>
                            Are you sure you want to delete the project "{selectedProject?.title}"?
                        </Typography>
                        <Typography variant="body2" sx={{ color: '#856404' }}>
                            This action cannot be undone. The project and all its data will be permanently removed.
                        </Typography>
                    </DialogContent>
                    <DialogActions sx={{ backgroundColor: '#fff3cd', px: 3, pb: 2 }}>
                        <Button 
                            onClick={handleCancelDelete}
                            sx={{ 
                                color: 'black',
                                borderColor: 'black',
                                '&:hover': {
                                    borderColor: 'black',
                                    backgroundColor: 'rgba(0, 0, 0, 0.04)'
                                }
                            }}
                        >
                            Cancel
                        </Button>
                        <Button 
                            onClick={handleConfirmDelete}
                            variant="contained"
                            color="error"
                            startIcon={<DeleteIcon />}
                            sx={{
                                backgroundColor: '#dc3545',
                                '&:hover': {
                                    backgroundColor: '#c82333'
                                }
                            }}
                        >
                            Delete Project
                        </Button>
                    </DialogActions>
                </Dialog>

                <Snackbar
                    open={snackbar.open}
                    autoHideDuration={6000}
                    onClose={() => setSnackbar({ ...snackbar, open: false })}
                >
                    <Alert
                        onClose={() => setSnackbar({ ...snackbar, open: false })}
                        severity={snackbar.severity}
                        sx={{ width: '100%' }}
                    >
                        {snackbar.message}
                    </Alert>
                </Snackbar>
            </Container>
        </>
    );
};

export default AdminProjects; 